
/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

/* Background Styles */
.pink-background {
  background-color: #FFC0CB;
  color: #8B0000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.black-background {
  background-color: #000;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Button Styles */
.button-group {
  display: flex;
  gap: 20px;
  margin: 20px 0;
}
.btn {
  padding: 10px 20px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}
.btn.yes {
  background-color: #4CAF50;
  color: white;
}
.btn.no {
  background-color: #f44336;
  color: white;
}
.btn.surprise {
  background-color: #FFD700;
  color: black;
  font-size: 20px;
}

/* Hover Effects */
.btn:hover {
  transform: scale(1.05);
  opacity: 0.9;
}

/* Image Gallery */
.image-gallery {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}
.image-gallery img {
  width: 250px;
  height: 350px;
  object-fit: cover;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s;
}
.image-gallery img:hover {
  transform: scale(1.1);
}
.evening-plans {
  list-style-type: none;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.8;
  margin: 20px 0;
}

.evening-plans li {
  background:rgba(196, 16, 115, 0.76);
  padding: 8px;
  border-radius: 12px;
  margin-bottom: 10px;
  transition: all 0.3s;
}

.evening-plans li:hover {
  background: rgba(228, 31, 129, 0.3);
  transform: scale(1.05);
}

p {
  font-size: 1.1rem;
  margin-bottom: 20px;
}
.reasons {
  margin-top: 20px;
  text-align: left;
  width: 320px;
  background: linear-gradient(135deg, #ffafbd, #ffc3a0);
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  animation: fadeIn 1.5s ease-out;
  transition: all 0.3s ease-in-out;
  font-family: 'Courier New', monospace;
  color: #4A235A;
}

.reasons summary {
  font-size: 1.4rem;
  font-weight: bold;
  color: #8B0000;
  cursor: pointer;
  transition: color 0.3s;
  text-align: center;
}

.reasons summary:hover {
  color: #FF69B4;
}

.reasons ul {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

.reasons li {
  background-color: rgba(255, 240, 246, 0.95);
  padding: 12px;
  margin-bottom: 10px;
  border-radius: 20px;
  font-size: 1.1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #5C3A6A;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.reasons li::before {
  content: "ðŸ’–";
  font-size: 1.3rem;
}

.reasons li:hover {
  transform: translateY(-4px);
  box-shadow: 0px 8px 16px rgba(255, 182, 193, 0.6);
  background-color: rgba(255, 240, 246, 0.99);
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}